  width: 25px;
  background-color: #eee;
  border: 2px solid black;
}

/* Turn background green when checked */
.check-container input:checked ~ .customBox {
  background-color: rgb(33, 243, 61);
  overflow: hidden;
}

/* Create the checkmark/indicator (hidden when not checked) */
.customBox:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.check-container input:checked ~ .customBox:after {
  display: block;
}

/* Style the checkmark/indicator */
.check-container .customBox:after {
  left: 7px;
  width: 5px;
  height: 15px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

