// Project tiles
.project-tile {
  position: relative;
  flex-shrink: 0;
  height: 425px;
  width: 260px;
  margin: 1rem;

  // Common properties on both divs
  .tile-info, .tile-demo {
    position: absolute;
    padding: .5rem;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    border-radius: 5px;
    transition: transform .5s;
  }

  // Top side of project tile
  .tile-info {
    background-color: #192231;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, .3)), 
      url(../images/bg-matrix.jpg);
    background-size: cover;
    text-align: center;
    color: white;
    z-index: 1;
    box-shadow: 0 36px 10px -20px rgba(0, 0, 0, .3);

    h2, h3 {
      border-top: 3px groove #15b0f6;
      border-bottom: 3px groove #15b0f6;
    }
    h3 {
      font-size: 1.3rem;
    }
    img {
      max-width: 100%;
      max-height: 12rem;
      padding: .5rem;
      border-radius: 20px;
    }
    .skills-list {
      display: flex;
      text-align: initial;
      font-size: 1.2rem;
      flex-direction: column;
      list-style: none;

      li {
        border-bottom: 1px groove #15b0f6;
        min-width: 10rem;

        .norank {
          font-size: 1rem;
          padding-right: .7rem;
          padding-left: .6rem;
          font-weight: bold;
          color: skyblue;
        }
      }
    }
  } // END TILE INFO
    
  // Underside of project tile
  .tile-demo {
    background-color: #15b0f6;
    background: url(../images/bg-mobo.jpg); 
    background-size: cover;

    // Demo and source buttons
    .btn-demo, 
    .btn-source {
      position: absolute;
      display: flex;
      text-align: center;
      bottom: 5%;
      font-weight: bold;
      min-width: 7rem;
      min-height: 50px;
      transform: translateX(-50%);
      border-radius: 100px;
      border: 2px groove black;

      img {
        padding-left: .2rem;
        height: 40px;
      }
      label {
        margin: auto .5rem;
      }
      &:hover {
        transition: .5s;
        background-color: #15b0f6;
        border: 2px solid cyan;
      }
    }
    .btn-demo {
      left: 72%;
    }
    .btn-source {
      left: 28%;
    }
  }

  // CSS for animation
  &:hover div:first-child {
    transform: translate(0, -10%);
  }
  &:hover div:nth-child(2) {
    transform: translate(0, 7%) scale(1.1);
  }

  // Center tile contents
  .center-content {
    display: flex;
    justify-content: center;
  }
}



